import React from 'react';
import CartItem from '../cartItem/CartItem';

const CartList = ({value}) => {

    const {cart} = value;

    return (
        <div className="container">
            {cart.map(item=>{
                return  <CartItem  key={item.id} item={item} value={value} />;

              } )}
                      
        </div>
    );
}

export default CartList;